.SwiperHolder {
    margin: 0;
    padding: 0;
    z-index: 999;
    width: 100%;
    height: 500px;
    background-color: rgba(240, 240, 240);
  }
  .swiper {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
  }
  
  .swiper-slide {
    text-align: center;
    font-size: 18px;
    background: var(--Secoundry);
    /* Center slide text vertically */
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .Swiper_S_h {
    width: 100%;
    height: 100%;
    display: grid;
  }
  
  .IMG_SSH {
    grid-column: 1/-1;
    grid-row: 1/-1;
    margin-left: auto;
    width: 80%;
    filter: blur(4px);
    z-index: 0;
  }
  .SSHDatas {
    margin-top: 250px;
    z-index: 1;
    grid-column: 1/-1;
    grid-row: 1/-1;
    height:250px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
  }
  .HeadLine_T {
    margin: 10px;
    padding: 10px;
    color: var(--Primary);
    font-weight: 500;
    font-size: 14px;
  }
  .Title_T {
    margin: 10px;
    padding: 10px;
    color: white;
    font-weight: bold;
  }
  
  .Button_HT{
    display: flex;
  }
  .WatchNow_T{
  
    margin: 20px;
    border: none;
    border-radius: 25px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    background-color: var(--Primary);
    transition: all 100ms linear;
  }
  .WatchNow_T:active{
    transform: scale(96%);
  }
  .WatchNow_T svg{
    fill: var(--Secoundry);
    font-size: 20px;
    padding: 5px;
  }
  .WatchNow{
    padding: 0px 10px 0px 5px;
    color: var(--Secoundry);
  }
  .Details_T{
  
    margin: 20px;
    border: none;
    border-radius: 25px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    background-color: var(--Primary);
    transition: all 100ms linear;
  }
  .Details_T:active{
    transform: scale(96%);
  }
  .Details_T svg{
    fill: white;
    font-size: 20px;
    padding: 5px;
  }
  .Details{
    padding: 0px 5px 0px 10px;
    color: white;
  }
  
  
  
  .Left {
    position: absolute;
    left: 0;
    width: 100px;
    bottom: 0;
    top: 0;
    z-index: 0;
    background-image: linear-gradient(
      to right,
      rgba(44, 44, 44, 0.8),
      rgba(0, 0, 0, 0)
    );
  }
  .Right {
    position: absolute;
    right: 0;
    width: 100px;
    bottom: 0;
    top: 0;
    z-index: 0;
    background-image: linear-gradient(
      to left,
      rgba(44, 44, 44, 0.8),
      rgba(0, 0, 0, 0)
    );
  }
  .Top {
    position: absolute;
    left: 0;
    height: 100px;
    right: 0;
    top: 0;
    z-index: 0;
  
    background-image: linear-gradient(
      to bottom,
      rgba(44, 44, 44, 0.8),
      rgba(0, 0, 0, 0)
    );
  }
  .Bottom {
    position: absolute;
    left: 0;
    height: 100px;
    bottom: 0;
    right: 0;
    z-index: 0;
    background-image: linear-gradient(
      to top,
      rgba(44, 44, 44, 0.8),
      rgba(0, 0, 0, 0)
    );
  }
  
