.SideBar{
    z-index: 1000;
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    display:none;
}
.SideBar_Nav{
    box-shadow: 0px -2px 5px rgba(0,0,0,0.5);
    z-index: 1100;
    width: 450px;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    background-color: var(--Secoundry);
    transition: all 100ms linear;
    transform: translateX(0%);
}
.Close_SideBar{
    margin-top: 25px;
    margin-bottom: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 15px;
    border: none;
    background-color: rgba(255,255,255,0.1);
    color: var(--Primary);
    transition: all 100ms linear;
}
.Close_SideBar svg{
    fill: var(--Primary);
    font-size: 15px;
    padding: 0px 2px 0px 2px;
}
.Close_SideBar p{
    padding: 0px 10px 0px 10px;
}
.Close_SideBar:active{
    transform: scale(96%);
}
.FUllBLUE{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(44,44,44,0.5);
    filter: blur(100px);
    -webkit-filter: blur(100px);
    z-index: -1;
}
@media (max-width:1100px){
    .SideBar_Nav{
        width: 300px;

    }
    .Close_SideBar{
        margin-top: 15px;
        margin-bottom: auto;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 15px;
        border: none;
        background-color: rgba(255,255,255,0.1);
        color: var(--Primary);
        transition: all 100ms linear;
    }
}