:root {
  --Primary: #50c440;
  --Secoundry: rgb(44, 44, 44);
}
.Nav {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 100px;
  box-shadow: 0px -2px 5px rgba(0, 0, 0, 0.5);
  background-color: var(--Secoundry);
  z-index: 1000;
}

.Navbar_nav {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.Nav_Bar_Search {
  position: fixed;
  top: 100px;
  left: 0;
  right: 0;
  height: 50px;
  background-image: linear-gradient(
    to bottom,
    rgb(80, 196, 64,0.6),
    rgba(0, 0, 0, 0)
  );
  display: none;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}
.Search_Nav {

  margin-right: 10px;
  margin-left: auto;
  padding: 1px 10px 1px 10px;
  display:flex;
  flex-direction: row;
}
.Inp_form {
  margin-left: 10px;
  margin-right: 10px;
  width: 80%;
  height: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.Input_Search {
  outline: none;
  border: none;
  background-color: var(--Secoundry);
  border-radius: 10px;
  width: 100%;
  height: 100%;
  color: white;
}
::placeholder {

  color: var(--Primary);
  opacity: 1; /* Firefox */
}

:-ms-input-placeholder {

  /* Internet Explorer 10-11 */
  color: var(--Primary);
}

::-ms-input-placeholder {
  /* Microsoft Edge */

  color:var(--Primary);
}
@media (max-width: 1100px) {
  .Nav {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: 75px;
    box-shadow: 0px -2px 5px rgba(0, 0, 0, 0.5);
    background-color: var(--Secoundry);
  }

  .Navbar_nav {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .Nav_Bar_Search {
    position: fixed;
    top: 75px;
    left: 0;
    right: 0;
    height: 50px;
  }
}
